@import '../../styles/utils.scss';

.viewer-template{
    header{
        background: $oc-gray-9;
        height:  5rem;
        color: #fff;
        padding:1rem;

        display: flex;
        align-items: center;

        font-size: 2rem;
        font-weight: 600;

        @include media("<tablet"){
            font-size:1.25rem;
        }
    }
    .viewer-wrapper{
        position: relative;
        width:100%;
        height: calc(100vh - 5rem);
    }
}